<template>
    <div class="main-page">
        <div class="main-page-content">
            <div class="content">
                <div class="title">
                    <div class="text">集卡抽大奖</div>
                </div>
                <!-- <div class="content-text" :style="{top:basicH+'px'}">集齐五张卡片即可开始抽奖</div> -->
                <div class="content-inner">
                    <div class="content-inner-box" :style="{top:basicH +'px'}">
                        <div class="content-inner-cards">
                            <div class="card-item" v-for="(item,index) in options" :key="index">
                                <div class="card-item-top">
                                    <img :src="item.Img" alt="" v-if="myOptionIds.includes(item.Id)">
                                </div>
                                <div class="card-item-bottom hidden-one">{{item.Code}}</div>
                                <!-- <div class="card-item-num" :class="myOptionIds.includes(item.Id)&&'active'">{{myOptionIds.includes(item.Id)?1:0}}</div> -->
                            </div>
                        </div>
                        <div class="content-text">{{info.Intro}}</div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: "QsmsCard",
    props: {},
    components: {},
    data() {
        return {
            basicH: 0,
            options: [],
            myOptionIds: [],
            info: {}
        }
    },
    methods: {
        getBasicH() {
            let ele = document.getElementsByClassName("content-inner")[0]
            this.basicH = ele.offsetWidth * 480 / 680 + 80
        },
        QSMS_QuestionnairePersonVotingOption() {
            this._api.QSMS_QuestionnairePersonVotingOption({}, this.$route.query).then(res => {
                if (res) {
                    this.QSMS_QuestionnairePersoniDetails()
                }
            })
        },
        QSMS_QuestionnairePersoniDetails() {
            return this._api.QSMS_QuestionnairePersoniDetails({ QuestionnaireId: this.$route.query.QuestionnaireId }).then(res => {
                if (res && res.data) {
                    this.info = res.data
                    if (res.data.VotingRecordList) this.myOptionIds = res.data.VotingRecordList.map(it => it.OptionId)
                    for (let item of res.data.QuestionList) {
                        if (!this.$route.query.QuestionId) {
                            this.options = this.options.concat(item.QuestionOptionList)
                        } else {
                            if (item.Id == this.$route.query.QuestionId) {
                                this.options = item.QuestionOptionList
                            }
                        }
                    }
                }
            })
        }
    },
    async mounted() {
        this.getBasicH()
        await this.QSMS_QuestionnairePersoniDetails()
        if (this.$route.query.OptionId) {
            let nowT = new Date().getTime()
            if (nowT > this.$route.query.StartTime) return this.$u.msg("活动已过期！", "/static/img/warning.png")
            this.QSMS_QuestionnairePersonVotingOption()
        }
    },
}
</script>

<style lang="scss" scoped>
.main-page {
    background: url("../../../public/static/img/qsms/1.png") no-repeat;
    background-size: cover;
    overflow: hidden;

    .main-page-content {
        width: 100%;
        height: 100%;
        overflow-y: auto;
        overflow-x: hidden;
    }

    .content {
        position: relative;
        height: calc(100% - 80px);
        width: calc(100% - 30px);
        margin-left: 15px;

        .title {
            height: 80px;

            .text {
                display: inline-block;
                width: 50%;
                height: 40px;
                text-align: center;
                line-height: 40px;
                color: #f9ee6b;
                font-weight: 800;
                margin-top: 30px;
                margin-left: 50%;
                transform: translate(-50%, 0);
                background: url("../../../public/static/img/qsms/6.png")
                    no-repeat;
                background-size: 100% 100%;
            }
        }

        .content-inner {
            width: 100%;
            height: 100%;
            background: url("../../../public/static/img/qsms/7.png") no-repeat;
            background-size: 100%;
        }

        .content-inner-box {
            position: absolute;
            width: 100%;
            padding-bottom: 20px;

            .content-inner-cards {
                display: flex;
                flex-wrap: wrap;
                justify-content: center;
                align-content: flex-start;
                width: 100%;

                .card-item {
                    position: relative;
                    width: 100px;
                    height: 140px;
                    background: #ad030b;
                    border-radius: 6px;
                    margin: calc((100% - 300px) / 8);

                    .card-item-top {
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        width: 72px;
                        height: 100px;
                        margin: 10px auto 0;
                        // background: url("../../../public/static/img/qsms/3.png")
                        //     no-repeat;
                        background-size: 100% 100%;
                        overflow: hidden;

                        img {
                            width: 100%;
                        }
                    }
                    .card-item-bottom {
                        line-height: 30px;
                        text-align: center;
                        color: #f1d6bc;
                        padding: 0 10px;
                    }
                    .card-item-num {
                        position: absolute;
                        top: 5px;
                        right: 10px;
                        width: 24px;
                        height: 24px;
                        line-height: 20px;
                        border: 2px solid #dedada;
                        background: #847b7d;
                        color: #dedada;
                        border-radius: 50%;
                        text-align: center;

                        &.active {
                            background: #dc4046;
                        }
                    }
                }
            }

            .btn {
                display: inline-block;
                height: 40px;
                line-height: 40px;
                color: #7e4827;
                font-weight: 800;
                padding: 0 50px;
                border-radius: 15px;
                background: linear-gradient(
                    #ffe49c 3%,
                    #e69818 90%,
                    #dc9a2e 90%
                );
                margin: 20px auto 30px 50%;
                transform: translate(-50%, 0);
                white-space: nowrap;
            }
        }

        .content-text {
            // position: absolute;
            margin-top: 20px;
            top: 34%;
            margin-left: 50%;
            transform: translate(-50%, 0);
            display: inline-block;
            height: 30px;
            line-height: 30px;
            padding: 0 20px;
            border-radius: 15px;
            color: #c90a0f;
            text-shadow: 0px 0px 10px #e16547;
            font-weight: 800;
            white-space: nowrap;
            background: #ffe097;
        }
    }
}
</style>